@import url(https://fonts.googleapis.com/css?family=Liu+Jian+Mao+Cao&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", "Roboto";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
  position: relative;
}
.form input {
  background-color: #e6e0e0;
  color: #010f0d;
  font-size: 1.2rem;
  margin: 10px;
  padding: 5px 10px;
  border-radius: 5px;
  outline: none;
}
.form input[type="text"] {
  width: 250px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.form input:focus {
  background-color: #ffffff;
}

.form label {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.priority {
  display: inline-block;
  margin-left: 10px;
}
.priority label {
  font-size: 1.2rem;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.priority label:after {
  content: "";
  display: block;
  border-bottom: 3px solid rgb(232, 65, 24, 1);
  height: 2px;
  width: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.priority label:hover:after {
  width: 100%;
}
.priority label * div:after {
  background-color: black;
}

input[type="checkbox"] {
  position: relative;
  width: 24px;
  height: 24px;
  margin: 0 auto;
  cursor: pointer;
  opacity: 0.7;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

input[type="checkbox"]::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  border-radius: 5px;
  background-color: rgb(232, 65, 24, 0.5);
  box-shadow: 0px 0px 0px 5px rgb(232, 65, 24, 1);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
input[type="checkbox"]:checked {
  opacity: 1;
}

input[type="checkbox"]:checked::after {
  background-color: transparent;
  box-shadow: 0px 0px 3px 5px rgb(232, 65, 24, 1);
}
.priority {
  color: rgb(156, 5, 0);
}

.add {
  background-color: transparent;
  display: block;
  margin: 10px auto;
  border: 2px solid rgb(54, 8, 8);
  font-size: 2rem;
  font-weight: bold;
  padding: 5px 35px;
  border-radius: 5px;
  box-shadow: inset 0px 0px 0px 1px rgb(54, 8, 8);
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.add:hover {
  -webkit-animation: buttonAnimation linear infinite 5s alternate;
          animation: buttonAnimation linear infinite 5s alternate;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes buttonAnimation {
  0% {
    box-shadow: inset 0px 0px 0px 2px rgb(54, 8, 8);
  }
  10% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  20% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  40% {
    box-shadow: inset 0px 0px 10px 2px rgb(54, 8, 8);
  }
  60% {
    box-shadow: inset 0px 0px 10px 2px rgb(54, 8, 8);
  }
  80% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  90% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  100% {
    box-shadow: inset 0px 0px 0px 2px rgb(54, 8, 8);
  }
}

@keyframes buttonAnimation {
  0% {
    box-shadow: inset 0px 0px 0px 2px rgb(54, 8, 8);
  }
  10% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  20% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  40% {
    box-shadow: inset 0px 0px 10px 2px rgb(54, 8, 8);
  }
  60% {
    box-shadow: inset 0px 0px 10px 2px rgb(54, 8, 8);
  }
  80% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  90% {
    box-shadow: inset 0px 0px 5px 2px rgb(54, 8, 8);
  }
  100% {
    box-shadow: inset 0px 0px 0px 2px rgb(54, 8, 8);
  }
}

.activeTask,
.deactiveTask {
  position: relative;
  min-width: 200px;
  width: 50vw;
  margin: 10px auto;
  padding: 10px 5px;
  background-color: #fff;
  text-align: left;
  border-radius: 7px;
  box-shadow: inset 0 0 7px 1px black;
}
.deactiveTask {
  background-color: rgb(230, 223, 223, 0.4);
}
.activeTask p,
.deactiveTask p {
  overflow-wrap: break-word;
  white-space: pre-line;
  padding-right: 100px;
  padding-left: 5px;
}

.activeTask strong,
.deactiveTask strong {
  margin-right: 10px;
}
.activeTask span,
.deactiveTask span {
  margin: 0 5px;
  white-space: nowrap;
}
.deactiveTask em {
  margin: 0 10px;
}
.activeTask button,
.deactiveTask button {
  position: absolute;
  top: 50%;
  right: 3%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 10px;
  background-color: transparent;
  border: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.activeTask button + button {
  right: 0;
}
.deactiveTask button {
  right: 0;
}
.activeTask button:hover,
.deactiveTask button:hover {
  border: 1px solid rgb(154, 236, 219, 1);
  background-color: rgb(154, 236, 219, 1);
  border-radius: 15px;
  box-shadow: inset 0px 0px 10px 3px rgb(54, 8, 8);
  color: white;
}

@media (max-width: 620px) {
  .activeTask,
  .deactiveTask {
    width: 75vw;
  }
  .activeTask p,
  .deactiveTask p {
    padding-right: 5px;
    padding-left: 15px;
  }
  .activeTask button,
  .deactiveTask button {
    position: relative;
    display: inline-block;
    width: 50%;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    background-color: rgb(154, 236, 219, 0.2);
    padding: 5px 0;
    border-radius: 2px;
  }
  .activeTask button:hover,
  .deactiveTask button:hover {
    border: none;
    background-color: rgb(154, 236, 219, 0.6);
    border-radius: 2px;
    box-shadow: none;
  }
  .deactiveTask button {
    display: block;
    width: 100%;
  }
  .deactiveTask span,
  .activeTask span {
    display: block;
    width: 100%;
    text-align: center;
    margin: 0;
  }
}

.active,
.deactive {
  width: 75%;
  margin: 0 auto;
}
.deactive {
  border-top: 2px solid rgba(15, 32, 29, 0.3);
  padding-top: 5px;
}
.active h1 {
  font-size: 1.6rem;
}
.active h1 + p {
  margin: 10px;
  font-size: 1.2rem;
}

.deactive h3 {
  font-size: 1.4rem;
}
.deactive span {
  margin: 0;
}
@media (max-width: 620px) {
  .active,
  .deactive {
    width: 90%;
  }
}

.App {
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  padding: 20px 0;
  background-color: #9aecdb;
  box-shadow: 0 0 10px 1px black;
  text-align: center;
}
.App h2 {
  font-family: "Liu Jian Mao Cao", cursive;
  font-size: 3rem;
  padding-bottom: 20px;
}

